import { isSameDay, isValid } from 'date-fns';
import { isNil } from "../../../../utils/isNil";
/**
 * Проверяет даты интервала со входной датой на пересечение по дням и возвращает строку
 * в соответствии с тем, на какую из дат интервала выпадает входная дата.
 * Если пересечение отсутствует, возвращается undefined
 */
export function getIntervalIntersectionType(interval, comparable) {
    if (isNil(interval)) {
        return null;
    }
    var start = interval.start, end = interval.end;
    if (isNil(comparable) || !isValid(comparable)) {
        return null;
    }
    if (isNil(start) || !isValid(start)) {
        return null;
    }
    if (isNil(end) || !isValid(end)) {
        return null;
    }
    if (isSameDay(comparable, start) && isSameDay(comparable, end)) {
        return 'same';
    }
    if (isSameDay(comparable, start)) {
        return 'first';
    }
    if (isSameDay(comparable, end)) {
        return 'last';
    }
    return null;
}
