/**
 * Возвращает локализированное и капитализированное значение переданного месяца.
 * Используется в отрисовке таблицы выбора месяца и поле выбранного периода.
 */
export function getMonthLocale(month) {
    return new Date(Date.UTC(null, month))
        .toLocaleString('ru', { month: 'long' })
        .replace(/^./g, function (l) { return l.toUpperCase(); });
}
